@import url(https://fonts.googleapis.com/css2?family=Assistant:wght@300;400;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@400;500;600;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


a {
  text-decoration: none;
}

p {
  color: rgb(108, 108, 108);
  line-height: 1.6;
  margin: 0;
}

header h1,
h2 {
  color: #282b42;
}

h1,
h2,
h4 {
  margin: 0;
}

header a {
  font-size: 1.225rem;
}

.title {
  margin: 0;
  padding: 1rem;
}

.link {
  font-weight: 600;
  letter-spacing: 0.0325rem;
  color: #c95050;
  box-shadow: inset 0 -0.0625rem 0 0 white, inset 0 -0.1875rem 0 0 #ecb4b4;
  transition: box-shadow 0.15s ease;
}

.link:hover {
  transition: 0.2s ease;
  box-shadow: inset 0 -0.0625rem 0 0 white, inset 0 -0.1875rem 0 0 #c95050;
}

.nav {
  display: flex;
  justify-content: space-between;
  padding: 1rem;
}

nav img {
  width: 80px;
}

nav ul {
  display: flex;
  align-items: center;
  height: 100%;
  flex-wrap: wrap;
}

nav ul li {
  margin: 0 2.5rem 0 0;
}

nav ul li:last-child {
  margin: 0;
}

nav a {
  text-decoration: none;
  font-size: 17px;
  color: #6e7082;
  transition: color 0.2s ease;
  border-bottom: 1.5px solid transparent;
  font-weight: 600;
  letter-spacing: 0.0325rem;
  transition: color 0.2s ease;
}

nav a:hover {
  transition: color 0.2s ease;
  color: #282b42;
  box-shadow: inset 0 0 0 0 white, inset 0 -0.095rem 0 0 #c95050;
  transition: 0.2s;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(18rem, 1fr));
  /* grid-gap: 1em; */
}

.grid a {
  padding: 1rem;
}

.card {
  position: relative;
  width: auto;
  height: 21rem;
  display: inline-grid;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr;
  /* border: lightblue 2px solid; */
  box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 3px 0px,
    rgba(0, 0, 0, 0.05) 0px 5px 15px 0px;
  border-radius: 0.375rem;
  transition: 0.3s ease;
  /* background-color: lightgray; */
}

.card:hover {
  cursor: pointer;
}

.card:hover .project-thumb {
  opacity: 0.8;
  transition: 0.3s ease;
}

.card:hover article h2 {
  color: #c95050;
  transition: 0.3s;
}

article {
  padding: 1rem;
}

article h6 {
  display: inline-block;
  text-transform: uppercase;
  font-size: 0.75rem;
  line-height: 1;
  letter-spacing: 0.0625rem;
  font-weight: 600;
  margin: 0;
  border-bottom: 0.105rem solid rgb(223, 223, 223);
  font-family: "Source Code Pro", monospace;
  color: #777;
}

article h2 {
  font-size: 1.425rem;
  font-weight: 700;
  margin: 0.625rem 0;
  line-height: 1;
  color: #282b42;
  -webkit-text-decoration: underline transparent;
          text-decoration: underline transparent;
}

.image-wrapper {
  border-top-left-radius: 0.375rem;
  border-top-right-radius: 0.375rem;
}

.project-thumb {
  border-top-left-radius: 0.375rem;
  border-top-right-radius: 0.375rem;
  background-size: cover;
  background-position: center center;
}

header .container {
  padding: 0 1.75rem 0 1.75rem;
}

.illustrations-grid {
  display: grid;
  grid-template-columns: repeat(11, 1fr);
  grid-template-rows: repeat(11, 5vw);
  grid-gap: 1rem;
  flex-direction: column;
  flex-flow: row wrap;
  padding: 1rem;
}

.illustrations-grid img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.item-1 {
  grid-column-start: 5;
  grid-column-end: 12;
  grid-row-start: 5;
  grid-row-end: 8;
}

.item-2 {
  grid-column-start: 6;
  grid-column-end: 12;
  grid-row-start: 1;
  grid-row-end: 5;
}

.item-3 {
  grid-column-start: 1;
  grid-column-end: 6;
  grid-row-start: 1;
  grid-row-end: 5;
}

.item-4 {
  grid-column-start: 1;
  grid-column-end: 5;
  grid-row-start: 5;
  grid-row-end: 12;
}

.item-5 {
  grid-column-start: 5;
  grid-column-end: 12;
  grid-row-start: 8;
  grid-row-end: 12;
}
.item-6 {
  grid-column-start: 5;
  grid-column-end: 9;
  grid-row-start: 5;
  grid-row-end: 12;
}

.about {
  display: flex;
}
.about-heading {
  width: 15rem;
}

.about-heading h2 {
  margin: 0;
}

.about-body p {
  margin-bottom: 1rem;
}

.about-body {
  /* width: 40rem; */
  padding: 1rem;
}

.footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  color: #c95050;
  letter-spacing: 0.0325rem;
}

.social {
  margin-left: 0;
}
.footer img {
  width: 1.35rem;
  margin-left: 1rem;
  opacity: 0.6;
  transition: 0.3s ease;
}
.social {
  margin-left: 0 !important;
}
/* .footer img:first-of-type {
  margin-left: 0;
} */

.footer img:hover {
  opacity: 1;
  transition: 0.3s ease;
}

footer div,
h4 {
  padding: 1rem;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Assistant", sans-serif;
  -webkit-font-smoothing: antialiased;
}

html {
  scroll-behavior: smooth;
}

* {
  box-sizing: border-box;
}
ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

figure {
  margin: 0;
}

.container {
  max-width: 58rem;
  margin: auto;
  width: 100%;
  padding: 0;
  padding: 1.75rem 1rem;
  /* background-color: lightblue; */
}

/* 768px */
@media (max-width: 64rem) {
  .container {
    max-width: 52rem;
  }
  .about {
    display: flex;
    flex-direction: column;
  }
}
/* 512px */
@media (max-width: 36rem) {
  .icon {
    display: none;
  }
  .nav {
    display: block;
  }
}

